import { CUqBase } from "uq-app";
import { VTicketTypeList } from "./VTicketTypeList";

export class CTicketType extends CUqBase {
    ticketTypes = [
        {
            name: '',
            arr: [
                {
                    name: '',
                },
                {
                    name: '',
                }
            ]
        }
    ];

    list: any[];

    protected async internalStart(param?: any, ...params: any[]): Promise<void> {
        let { JsTicket } = this.uqs;
        this.list = await JsTicket.QueryID({
            ID: JsTicket.TicketType,
            page: {
                start: 0,
                size: 1000,
            }
        })
        this.openVPage(VTicketTypeList);
    }

    saveTicketTypes = async () => {
        let retIds: number[] = [];
        for (let tt of this.ticketTypes) {
            let ret = await this.uqs.JsTicket.SaveTicketType.submitReturns({
                typeName: tt.name,
                typeVice: undefined,
                states: tt.arr.map(v => ({
                    stateName: v.name,
                    stateVice: undefined,
                })),
            });
            retIds.push(ret.ret[0].id);
        }
        alert(JSON.stringify(retIds));
    }
}
