import { VPage } from "tonwa";
import { Customer } from "uq-app/uqs/JsTicket";
import { CUqTest } from "uqTest";

export class VUqTest extends VPage<CUqTest> {
    header() {
        return 'UQ';
    }
    content() {
        let customer: Customer = {
            no: '1013',
            name: '',
            vice: '3',
        };
        return <div className="p-3">
            <div className="mb-3">
                <button className="btn btn-primary"
                    onClick={() => this.controller.newCustomer(customer)}></button>
            </div>
            <div className="mb-3">
                <button className="btn btn-primary"
                    onClick={() => this.controller.listCustomer()}></button>
            </div>
            <div className="mb-3">
                <button className="btn btn-primary"
                    onClick={() => this.controller.loadTvCustomer()}>Tv</button>
            </div>
            <div className="mb-3">
                <button className="btn btn-primary"
                    onClick={() => this.controller.changeCustomer()}></button>
            </div>
        </div>
    }
}
